import http from '../../http-common'

class banksSercice {
  getAll () {
    return http.get('Banks')
  }

  getAllPagnation (params) {
    return http.get('BanksPaging', { params })
  }

  get (id) {
    return http.get(`Banks?Id=${id}`)
  }

  create (data) {
    return http.post('Banks', data)
  }

  update (id, data) {
    return http.put(`Banks?Id=${id}`, data)
  }

  delete (id) {
    return http.delete(`Banks?Id=${id}`)
  }
}

// eslint-disable-next-line new-cap
export default new banksSercice()
